.projectsContainer {
  width: 100%;
  height: auto;
  padding-top: 3%;
  padding-bottom: 2%;
}

.projectsContainer .projectheading {
  width: 100%;
  text-align: center;
}

.projectsContainer .projectheading h1 {
  text-align: center;
  font-family: "Courgette", cursive;
  font-weight: bolder;
  font-size: 45px;
  width: 200px;
  margin: auto;
  border-bottom: 2px solid rgb(19, 5, 70);
  color: rgb(19, 5, 70);
  margin-bottom: 5px;
}

.projectBox {
  padding: 3%;
}

.projectItem {
  display: flex;
  justify-content: space-evenly;
  padding: 2%;
  box-shadow: rgba(6, 24, 44, 0.4) 0px 0px 0px 2px,
    rgba(6, 24, 44, 0.65) 0px 4px 6px -1px,
    rgba(255, 255, 255, 0.08) 0px 1px 0px inset;
  width: 90%;
  margin: auto;
  background-color: white;
  border-radius: 17px;
  padding-bottom: 4%;
}

.projectItem1 {
  display: flex;
  justify-content: space-evenly;
  margin: auto;
  flex-direction: row-reverse;
  padding: 2%;
  box-shadow: rgba(6, 24, 44, 0.4) 0px 0px 0px 2px,
    rgba(6, 24, 44, 0.65) 0px 4px 6px -1px,
    rgba(255, 255, 255, 0.08) 0px 1px 0px inset;
  width: 90%;
  background-color: white;
  border-radius: 17px;
  padding-bottom: 4%;
}

.projectItem:hover {
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px, rgb(51, 51, 51) 0px 0px 0px 3px;
  transform: scale(1.03);
  transition: 0.5s ease-in;
}

.projectItem1:hover {
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px, rgb(51, 51, 51) 0px 0px 0px 3px;
  transform: scale(1.03);
  transition: 0.5s ease-in;
}

.functionality {
  display: none;
  text-align: left;
}

.functionality > p {
  margin: 0.5%;
  font-weight: 500;
  font-size: 12px;
  color: rgb(1, 1, 67);
}

.functionality > p > span {
  color: rgb(246, 2, 10);
}

.projectDetails {
  width: 45%;
  display: block;
  text-align: center;
}

.projectDetails > h2 {
  display: flex;
  color: rgb(6, 84, 84);
}

.projectImage {
  width: 45%;
  display: block;
}

.projectImage > img {
  width: 100%;
  height: 322px;
  border: 3px solid black;
  border-radius: 20px;
}

.desc {
  text-align: left;
  font-size: 90%;
}

.desc > span {
  font-size: large;
  font-weight: bold;
}

.techStacks .btn {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.techStacks .btn > button {
  width: 150px;
  margin: 2%;
  border-radius: 10px;
  background-color: rgb(194, 31, 91);
  color: white;
}

.techStacks .btn > button:hover {
  background-color: rgb(8, 0, 0);
  color: rgb(255, 254, 254);
  transform: scale(1.06);
  transition: 0.4s ease-in;
}

.techStacks .btn > button > h3 {
  padding: 1%;
  margin: 1%;
}

.techStacks .btn > button > h3 > a {
  text-decoration: none;
  color: inherit;
}

.techStacks > .tech-stacks {
  display: flex;
  flex-direction: column;
  gap: 3%;
}

.techStacks > .tech-stacks > h3 {
  font-size: 120%;
  color: rgb(134, 8, 8);
}

.techStacks > .tech-stacks > div {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  width: 100%;
  gap: 10% 4%;
}

.languages {
  box-shadow: rgba(6, 24, 44, 0.4) 0px 0px 0px 2px,
    rgba(6, 24, 44, 0.65) 0px 4px 6px -1px,
    rgba(255, 255, 255, 0.08) 0px 1px 0px inset;
  display: flex;
  width: 100%;
  border-radius: 6px;
  flex-direction: column;
}

.languages:hover {
  transform: scale(1.06);
  border-radius: 15px;
  transition: 0.4s ease-in;
}

.languages > div {
  width: 100%;
}

.languages > div img {
  width: 60%;
  align-self: center;
  margin: auto;
  margin-top: 10%;
  border-radius: 20%;
}

.languages > div p {
  font-size: 7px;
  padding: 1%;
  margin: 0% auto 10% auto;
  font-weight: bolder;
  color: rgb(153, 6, 69);
}

.techStacks {
  display: flex;
  justify-content: space-evenly;
  gap: 2%;
}

.project-type {
  font-size: 160%;
  color: rgb(153, 6, 69);
  font-weight: bold;
}

.project-type > span {
  color: blue;
}

@media all and (min-width: 901px) and (max-width: 1150px) {
