.ContactContainer {
  background-color: #0d1117;
  height: 140vh;
  color: white;
  text-align: center;
  /* padding-top: 2%; */
  display: flex;
  align-items: center;
  justify-content: center;
}

.contactlogotop {
  color: rgb(246, 28, 83);
  font-size: 25px;
}

.ContactContainer > div > h1 {
  text-align: center;
  font-family: "Courgette", cursive;
  font-weight: bolder;
  font-size: 45px;
  width: 250px;
  border-bottom: 2px solid rgb(237, 235, 243);
  color: rgb(250, 173, 5);
  margin: 0% auto 5% auto;
  /* padding-top: 3%; */
}

.contactlogo {
  border: 1px solid white;
  font-size: 40px;
  padding: 1% 2% 1% 2%;
  border-radius: 8px;
  color: rgb(241, 194, 4);
  margin: 2%;
}

.contactlogo:hover {
  transform: scale(1.07);
  transition: 0.5s ease-in;
  border: 1px solid rgb(252, 4, 4);
}

.ContactContainer div div h3 a {
  text-decoration: none;
  color: white;
}

.thank-you {
  font-family: "Courgette", cursive;
  color: rgb(192, 5, 86);
  /* padding-bottom: 10%; */
}

.text {
  font-size: 20px;
  margin: 10px;
  color: rgb(241, 194, 4);
}

form {
  max-width: 420px;
  margin: 50px auto;
  background-color: #0d1117;
}

.feedback_input {
  color: white;
  font-family: Helvetica, Arial, sans-serif;
  font-weight: 500;
  font-size: 18px;
  border-radius: 5px;
  line-height: 22px;
  background-color: transparent;
  border: 2px solid #dbd2d2;
  transition: all 0.3s;
  padding: 13px;
  margin-bottom: 15px;
  width: 100%;
  box-sizing: border-box;
  outline: 0;
}

.feedback_input::placeholder {
  color: white;
}

.feedback_input:focus {
  border: 2px solid #eedfdf;
}

textarea {
  height: 150px;
  line-height: 150%;
  resize: vertical;
}

[type="submit"] {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  width: 100%;
  background: #2934d3;
  border-radius: 5px;
  border: 0;
  cursor: pointer;
  color: white;
  font-size: 24px;
  padding-top: 10px;
  padding-bottom: 10px;
  transition: all 0.3s;
  margin-top: -4px;
  font-weight: 700;
}
[type="submit"]:hover {
  background: #5e5555;
}

@media all and (min-width: 450px) and (max-width: 800px) {
  .ContactContainer {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .ContactContainer > div > h1 {
    font-size: 35px;
    width: 250px;
  }

  .contactlogo {
    border: 1px solid white;
    font-size: 40px;
    padding: 1% 2% 1% 2%;
    border-radius: 8px;
    color: rgb(226, 250, 11);
    margin: 2%;
  }
}

@media all and (min-width: 280px) and (max-width: 450px) {
  .ContactContainer {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .ContactContainer > div {
    padding: 3%;
  }

  .ContactContainer > div > div > h3 {
    font-size: 15px;
  }

  .ContactContainer > div > h1 {
    font-size: 23px;
    width: fit-content;
    padding-left: 2%;
    padding-right: 2%;
    margin-bottom: 10%;
  }

  .contactlogo {
    font-size: 30px;
    padding: 2% 3% 2% 3%;
    color: red;
    margin: 2%;
    margin-top: 5%;
  }

  .contactlogo:hover {
    transform: scale(1.07);
    transition: 0.5s ease-in;
    border: 1px solid rgb(224, 224, 3);
  }
}

@media all and (min-width: 250px) and (max-width: 370px) {
  .ContactContainer {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .ContactContainer > div > div > h3 {
    font-size: 10px;
  }

  .contactlogotop {
    color: rgb(246, 28, 83);
    font-size: 18px;
  }

  .ContactContainer > div > h1 {
    font-size: 25px;
    width: fit-content;
    padding-left: 2%;
    padding-right: 2%;
    margin-bottom: 9%;
    margin-bottom: 15%;
  }
}
